'use client';

import { useState, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { motion, AnimatePresence } from 'framer-motion';
import { onboardingAPI } from '@/lib/api/onboarding';
import { useAuthContext } from '@/lib/auth/authContext';
import StepGoals from './steps/StepGoals';
import StepSkills from './steps/StepSkills';
import StepLearnTeach from './steps/StepLearnTeach';
import StepLookingFor from './steps/StepLookingFor';
import StepAvailability from './steps/StepAvailability';
import StepMatches from './steps/StepMatches';

const STEPS = [
  { id: 'goals', title: 'Your Goals', subtitle: 'What drives you?' },
  { id: 'skills', title: 'Your Skills', subtitle: 'What do you know?' },
  { id: 'learn-teach', title: 'Learn & Teach', subtitle: 'Exchange knowledge' },
  { id: 'looking-for', title: 'Your Circle', subtitle: 'Who do you need?' },
  { id: 'availability', title: 'Availability', subtitle: 'When are you free?' },
  { id: 'matches', title: 'Your Matches', subtitle: 'Meet your circle' },
];

export default function OnboardingWizard() {
  const router = useRouter();
  const { updateUser, user } = useAuthContext();
  const [currentStep, setCurrentStep] = useState(0);
  const [direction, setDirection] = useState(1);
  const [saving, setSaving] = useState(false);
  const [stepData, setStepData] = useState<Record<number, Record<string, unknown>>>({});

  const handleStepComplete = useCallback(async (step: number, data: Record<string, unknown>) => {
    setSaving(true);
    try {
      setStepData(prev => ({ ...prev, [step]: data }));
      await onboardingAPI.updateStep(step, data);

      if (step < STEPS.length - 2) {
        setDirection(1);
        setCurrentStep(step + 1);
      } else if (step === STEPS.length - 2) {
        await onboardingAPI.completeOnboarding();
        setDirection(1);
        setCurrentStep(step + 1);
      }
    } catch (error) {
      console.error('Failed to save step:', error);
    } finally {
      setSaving(false);
    }
  }, []);

  const handleBack = useCallback(() => {
    if (currentStep > 0) {
      setDirection(-1);
      setCurrentStep(currentStep - 1);
    }
  }, [currentStep]);

  const handleFinish = useCallback(() => {
    if (user) {
      updateUser({ ...user, onboardingCompleted: true } as any);
    }
    router.replace('/');
  }, [router, user, updateUser]);

  const progress = ((currentStep + 1) / STEPS.length) * 100;

  const slideVariants = {
    enter: (dir: number) => ({
      x: dir > 0 ? 300 : -300,
      opacity: 0,
      scale: 0.95,
    }),
    center: {
      x: 0,
      opacity: 1,
      scale: 1,
    },
    exit: (dir: number) => ({
      x: dir > 0 ? -300 : 300,
      opacity: 0,
      scale: 0.95,
    }),
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50 dark:from-neutral-950 dark:via-neutral-900 dark:to-blue-950 flex flex-col">
      {/* Header */}
      <header className="px-6 pt-6 pb-2 flex items-center justify-between max-w-2xl mx-auto w-full">
        <div className="flex items-center gap-3">
          <div className="w-9 h-9 rounded-xl bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center">
            <span className="text-white font-bold text-sm">V</span>
          </div>
          <span className="text-sm text-neutral-500 dark:text-neutral-400 font-medium">
            {currentStep + 1} of {STEPS.length}
          </span>
        </div>
        {currentStep < STEPS.length - 1 && currentStep > 0 && (
          <button
            onClick={handleBack}
            className="text-sm text-neutral-500 hover:text-neutral-700 dark:text-neutral-400 dark:hover:text-neutral-200 transition-colors"
          >
            Back
          </button>
        )}
      </header>

      {/* Progress bar */}
      <div className="px-6 max-w-2xl mx-auto w-full">
        <div className="h-1.5 bg-neutral-200 dark:bg-neutral-800 rounded-full overflow-hidden">
          <motion.div
            className="h-full bg-gradient-to-r from-blue-500 to-purple-600 rounded-full"
            initial={{ width: 0 }}
            animate={{ width: `${progress}%` }}
            transition={{ duration: 0.5, ease: 'easeOut' }}
          />
        </div>
      </div>

      {/* Step content */}
      <div className="flex-1 flex flex-col items-center justify-center px-6 py-8 max-w-2xl mx-auto w-full">
        <AnimatePresence mode="wait" custom={direction}>
          <motion.div
            key={currentStep}
            custom={direction}
            variants={slideVariants}
            initial="enter"
            animate="center"
            exit="exit"
            transition={{ duration: 0.35, ease: [0.25, 0.46, 0.45, 0.94] }}
            className="w-full"
          >
            {/* Step header */}
            <div className="text-center mb-8">
              <motion.h1
                className="text-3xl font-bold text-neutral-900 dark:text-white mb-2"
                initial={{ y: 10, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.1 }}
              >
                {STEPS[currentStep].title}
              </motion.h1>
              <motion.p
                className="text-neutral-500 dark:text-neutral-400 text-lg"
                initial={{ y: 10, opacity: 0 }}
                animate={{ y: 0, opacity: 1 }}
                transition={{ delay: 0.2 }}
              >
                {STEPS[currentStep].subtitle}
              </motion.p>
            </div>

            {/* Step component */}
            {currentStep === 0 && (
              <StepGoals
                initialData={stepData[0]}
                onComplete={(data) => handleStepComplete(0, data)}
                saving={saving}
              />
            )}
            {currentStep === 1 && (
              <StepSkills
                initialData={stepData[1]}
                onComplete={(data) => handleStepComplete(1, data)}
                saving={saving}
              />
            )}
            {currentStep === 2 && (
              <StepLearnTeach
                initialData={stepData[2]}
                onComplete={(data) => handleStepComplete(2, data)}
                saving={saving}
              />
            )}
            {currentStep === 3 && (
              <StepLookingFor
                initialData={stepData[3]}
                onComplete={(data) => handleStepComplete(3, data)}
                saving={saving}
              />
            )}
            {currentStep === 4 && (
              <StepAvailability
                initialData={stepData[4]}
                onComplete={(data) => handleStepComplete(4, data)}
                saving={saving}
              />
            )}
            {currentStep === 5 && (
              <StepMatches onFinish={handleFinish} />
            )}
          </motion.div>
        </AnimatePresence>
      </div>

      {/* Step indicators */}
      <div className="pb-8 flex justify-center gap-2">
        {STEPS.map((_, i) => (
          <div
            key={i}
            className={`h-1.5 rounded-full transition-all duration-300 ${
              i === currentStep
                ? 'w-8 bg-blue-500'
                : i < currentStep
                ? 'w-1.5 bg-blue-300 dark:bg-blue-700'
                : 'w-1.5 bg-neutral-300 dark:bg-neutral-700'
            }`}
          />
        ))}
      </div>
    </div>
  );
}
